.App {
  font-family: sans-serif;
  text-align: center;
  padding: 0;
  margin: 0;
  border: 0;
}

.tela {
  margin: 0;
  padding: 0;
  background-color: rgba(4, 123, 153, 0.705);
  height: 100vh;
}

.tableGame {
  background-color: rgb(166, 6, 214);
  padding-bottom: 2rem;
}

.tableGame h4 {
  font-size: 1.7rem;
  padding-left: 3rem;
  color: white;
}

.tableGame p {
  padding-left: 3rem;
  font-size: 1.5rem;
  color: wheat;
}

.titleGame {
  font-size: 2.4rem;
  color: white;
  text-align: center;
  padding: 3rem;
}

.subtitleGame {
  font-size: 1.8rem;
  color: white;
  text-align: center;
  padding: 15px;
}

.buttonGame {
  display: flex;
  justify-content: center;
  padding: 3rem;
}

.buttonGame button {
  background-color: whitesmoke;
  border: 0.6px solid rgb(150, 110, 245);
  padding: 1rem 2rem;
  border-radius: 7px;
  font-size: 1.3rem;
}

.buttonGame button:hover {
  cursor: pointer;
  color: white;
  background-color: violet;
  font-weight: bold;
}

.gameBoard {
  background-color: coral;
  height: 100vh;
}

.gameBoard p {
  color: whitesmoke;
  font-size: 2rem;
  padding-top: 2.5rem;
}

.gameBoard button {
  background-color: rgb(145, 229, 235);
  padding: 1rem 1.5rem;
  margin: 1rem;
  font-size: 1.2rem;
  border-radius: 6px;
  border-color: rgb(189, 26, 167);
  font-weight: 600;
}

.gameBoard button:hover {
  background-color: rgb(6, 209, 107);
  color: yellow;
  cursor: pointer;
}

.resultGame {
  background-color: rgb(57, 184, 243);
  height: 100vh;
  margin: 0;
  padding: 0;
  border: 0;
}

.resultGame h3 {
  color: white;
  text-align: center;
  padding-top: 3rem;
  font-size: 1.6rem;
}

.resultGame p {
  padding: 1.5rem;
  font-size: 2rem;
  text-align: center;
}

.numberGame {
  background-color: seagreen;
}
